import * as types from "../store/mutations";
import {} from "../utils/cache";
import {delCache , putCache,getCache} from "../utils/cache";

import {miniRouterMap} from "./miniRouterMap.js"

export const USER_INFO_KEY = 'USER_INFO'

let Global = {}
// import vue from '../main'
Global.install = function(Vue) {

  // eslint-disable-next-line camelcase

  //去指定页面或者返回
  Vue.prototype.$toPage = function(name){
    if (name == -1){
      this.$router.go(-1)
    } else{
      this.$router.push({
        name: name
      })
    }
  }

    //创建长连接
    Vue.prototype.$communicationCreate = function(){
        try { JNB._requestNative("communication", "start") } catch (e) { }
    }
    //移除长连接
    Vue.prototype.$communicationClose = function(){
        try { JNB._requestNative("communication", "close") } catch (e) { }
    }

  //网络加载用户信息
  Vue.prototype.$networkUserInfo = function(){
    JNB.query({
      serverName: "userServer",
      cls: "LoginRegistrationModule",
      method: "getStoreSession",
      params: {},
      callback: (data) => {
        try {
          let result = JSON.parse(data)
          if (result.code === 200) {
            //添加用户信息到缓存
            putCache(USER_INFO_KEY, JSON.stringify(result.data))
            //跳转到主页
            this.$router.push({
              name: "index"
            })
          } else {
            this.$removeUserInfo()
          }
        } catch (e) {
          this.$toast.fail("网络错误");
        }
      }
    })
  }

  //移除用户信息
  Vue.prototype.$removeUserInfo = function(){
     delCache(USER_INFO_KEY)
     this.$communicationClose()
  }

  Vue.prototype.$loadUserInfo =  function (){
    let json = getCache(USER_INFO_KEY)
    let obj =  json ? JSON.parse(json) : null
    if (obj){
        if((obj.roleCode & 2) > 0) return obj
    }
    return null
  }

  //获取商品图片路径
  Vue.prototype.$request_img = function ({ context, arr, skuName = 'sku', spuName = 'spu', imageType = '200x200' }) {
    return new Promise((resolve, reject) => {
      try {
        let list = arr.map(c => {
          return {
            sku: c[skuName],
            spu: c[spuName]
          }
        })
        JNB.query({
          serverName: 'globalServer',
          cls: 'FileInfoModule',
          method: 'fileServerInfo',
          params: {
            list: list
          },
          callback: function (data) {
            let result = JSON.parse(data)
            let date = new Date()
            result.data.goodsFilePathList.forEach((c, index) => {
              context.$set(
                  arr[index],
                  "imgURl",
                  result.data.downPrev +
                  c +
                  "/" +
                  arr[index][skuName] +
                  "-" + imageType + ".jpg" +
                  '?' + date.getFullYear() + date.getMonth() + date.getDay()
              );
            });
            resolve(result)
          }
        })
      } catch (e) {
        reject(e)
      }
    })
  }

  Vue.prototype.$stringToDate = function (str) {
    var tempStrs = str.split(" ");
    var dateStrs = tempStrs[0].split("-");
    var year = parseInt(dateStrs[0], 10);
    var month = parseInt(dateStrs[1], 10) - 1;
    var day = parseInt(dateStrs[2], 10);
    var timeStrs = tempStrs[1].split(":");
    var hour = parseInt(timeStrs[0], 10);
    var minute = parseInt(timeStrs[1], 10);
    var second = parseInt(timeStrs[2], 10);
    var date = new Date(year, month, day, hour, minute, second);
    return date;
  }

  //根据企业码获取服务下标
  Vue.prototype.$ser_in = function(comp_id){
    return Math.floor((comp_id / 8192) % 65535)
  }

  Vue.prototype.$putBDUser = function(userStr){
    putCache(USER_INFO_KEY, userStr)
  }

  Vue.prototype.$getBDUser = function(){
    let json = getCache(USER_INFO_KEY)
    let obj =  json ? JSON.parse(json) : null
    if (obj){
      if((obj.roleCode & 8192) > 0) return obj
    }
    return null
  }

  Vue.prototype.$delBDUser = function(){
    delCache(USER_INFO_KEY)
  }

  /**
   * @param {Object} context:vue对象
   * @param {Object} obj:商品属性对象
   * @param {Number} num:-2加，-1减，其他值为input输入
   */
  Vue.prototype.$manageGoodNum = function(context,obj,num){
    
    //防止快速点击
    if(obj._noCanOperate){
      return
    }
    obj._noCanOperate = true;
    //判断用户登录或认证状态
    if(this.$loadUserInfo()){
      if (this.$loadUserInfo().comp && (this.$loadUserInfo().comp.authenticationStatus&256)>0 ){
        return new Promise((res,rej)=>{
          JNB.query({
            serverName: "orderServer"+this.$ser_in(this.$loadUserInfo().comp.storeId),
            cls: "AppModule",
            method: "addCartOption",
            params: {
              sku: obj.sku,
              number: num
            },
            callback :(data)=>{
              obj._noCanOperate = false;
              const dataStr = JSON.parse(data);
              obj.cart = dataStr.data||0;
              if(dataStr.code != 200){
                this.$toast.fail(dataStr.message);
              }else{
                res()
              }
            },
          })
        })
      }else{
        obj._noCanOperate = false;
        this.$toast.fail("请认证后再继续购买");
      }
    }else{
      obj._noCanOperate = false;
      this.$router.push({
        name: "login"
      })
    }
    
  }

  Vue.prototype.$openTel = function(qq){
    JNB._requestNative("openTel",qq)
  }

  //首页页面跳转
  Vue.prototype.$homePageJumps = function(item){
    switch (item.option) {
      case 99:
        if(item.route){
          this.$toast(item.route)
        }
        break;
      case 100: 
        this.$router.push({
          path: miniRouterMap[item.route]
        })
        break;
      case 101:
        //暂无活动页
        break;
      case 102:
        if(item.template == 204 && item.route){
          //缓存回到此页面时需要的信息
          let data = {
            scrollTop:0,//滚动条位置
            pageIndex:1,//页码
            brandno:item.route,//品牌号
            hideBrandBox:false,
            onlyActivity:true,
            canToggleDtlImg:true,//表示不是从品牌专区进入
          }
          data = JSON.stringify(data);
          sessionStorage.setItem(
            "goodsActivityHomeData",
            data
          );
          item.name = "品牌专区"
        }
        this.$router.push({
          path: '/goodsActivityHome',
          query:{
            codes:item.code,//根据code查商品数据
            template:item.template,//模板类型
            tag:item.name,//给商品添加的标签
            dtlImg:item.dtlImg//专区的banner图
          }
        })
        break;
      case 103:
        this.$router.push({
          path: '/classifi',
          query:{
            spuCodes:item.route,//根据code查商品数据
          }
        })
        this.$store.state.tabbarIndex=1;
        break;
      default:
        break;
    }
  }

}
export default Global