<template>
  <div id="app">
    <router-view />
  </div>
</template>
<script>

  import * as types from "./store/mutations";

  export default {
    async beforeCreate(){

    },
    data() {
      return {
        message: []
      }
    },
    mounted() {
      //网络访问用户拦截
      JNB._interceptor(this.userInterceptor)
      //页面路由拦截器
      this.createRouteInterceptor();
      //注册长连接
      JNB._register("communicationSysReceive",this.communicationSysReceive)
      JNB._register("communicationPayReceive",this.communicationPayReceive)
      this.checkBD();
    },
    methods: {
      createRouteInterceptor(){
        this.$router.beforeEach((to, from, next) => {
          if (!to.meta.ignoreAuth) {  // 判断该路由是否需要登录权限
           if (this.$loadUserInfo()) {
              next();
            }
           else {
              next({
                path: '/login',
                query: {redirect: to.fullPath}  // 将跳转的路由path作为参数，登录成功后跳转到该路由
              })
            }
          }
          else {
            next();
          }
        })
      },
      //对所有返回值做拦截操作
      userInterceptor(data){
        try {
          let resp = JSON.parse(data)
          if (resp.code === -2) {
            this.$removeUserInfo()
          }
        } catch (e) {  }
        return false;
      },
      //后台推送的长连接消息体-系统消息
      communicationSysReceive(message) {
        console.log("收到服务器推送的系统消息: " + message)
        this.$store.commit(types.ADD_MESSAGE_LIST, message)
      },
      //后台推送的长连接消息体-支付结果
      communicationPayReceive(message) {
        console.log("收到服务器推送的支付结果: " + message)
        this.$store.state.payResult = JSON.pause(message)
        this.$store.commit(types.PAY_RESULT, message)
      },
      checkBD(){
      if(this.$getBDUser()){
        this.$toPage('bdHome')
        }
      }
    }
  }
</script>
<style lang="stylus">
#app
  -webkit-font-smoothing antialiased
  -moz-osx-font-smoothing grayscale
  color #2c3e50
  user-select: none
  background-color #f5f5f5
  font-family Source Han Sans CN
  box-sizing border-box
  min-height 100vh
  font-size 28px
#nav
  padding 30px
  a
    font-weight bold
    color #2c3e50
    &.router-link-exact-active
      color #42b983
</style>
