
//更改store中state状态的唯一方法就是提交mutation,就很类似事件.每个mutation都有一个字符串类型的事件类型和一个回调函数，
// 我们需要改变state的值就要在回调函数中改变。我们要执行这个回调函数，那么我们需要执行一个相应的调用方法：store.commit

// tabbar 下标
export const SET_TABBAR_INDEX = 'TAB_INDEX'
// 后台消息列表
export const SET_MESSAGE_LIST = 'SET_MESSAGE_LIST'
//添加一条消息
export const ADD_MESSAGE_LIST = 'ADD_MESSAGE_LIST'
//当前支付结果JSON文本
export const PAY_RESULT = 'PAY_RESULT'

const mutations = {
  [SET_TABBAR_INDEX](state, index) {
    state.tabbarIndex = index
  },
  [ADD_MESSAGE_LIST](state, message) {
    state.message.push(message)
  },
  [SET_MESSAGE_LIST](state, message) {
    state.message = message
  },
  [PAY_RESULT](state, message){
    state.payResult = message
  }
}

export default mutations
