import Cookies from 'js-cookie'

const CACHE_TIME = 60 * 60 * 24 * 7

//判断浏览器
function isNative() {
    return !("undefined" == typeof native)
}

// 添加一个缓存
export function putCache (k,v) {
    if (isNative()) {
       native.putData(k,v)
    }else{
        Cookies.set(k, v, {
            path: '/',
            maxAge: CACHE_TIME
        })
    }
  return v
}

//获取一个缓存
export function getCache (k) {
    let v
    if (isNative()){
        // native.invoke(method, content, callbackId);
        v = native.getData(k)
    } else{
        v = Cookies.get(k)
    }
    return   v || null
}

//移除一个缓存
export function delCache (k) {
    if (isNative()){
        native.delData(k)
    } else{
        Cookies.remove(k)
    }

}